body{
  font-size: 2rem;
}

.wrapper {
  width: clamp(280px, 100%, 1350px);
  margin: 0 auto;
  padding: 0 $space-small;
}

input,
textarea,
label,
button {
  cursor: pointer;
}

a{
  text-decoration: none;
}

button {
  border: 0;
  &:hover {
    box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.75);
  }
}
